import React from 'react';
import '../../App.css';
import logo from '../../images/dp_logo_color.svg'
import ContactForm from '../ContactForm/ContactForm';
import { Link } from 'react-router-dom';
import Section from '../Section/Section';
import { Blob } from 'react-blob'
import './Home.css';
import { useEffect } from 'react';

export default function Home() {

    
    useEffect(() => {
        const anchor = window.location.href
        // Check if there is a hash and if an element with that id exists
        const el = anchor && document.getElementById(anchor.split('#')[1])
        if (el) {    
            el.scrollIntoView({behavior: "smooth"})
        }
    }, [window.location.href]) // Fires every time hash changes

    return (
        <>
            <div className="background-container">
                {/* this is the first section with logo and description*/}
                <div className="home-sec" >
                    <div className="homeFlex">

                        <img src={logo} alt=" background" className="bg-Logo" />

                        <div className="description-pos">
                            <h1>
                                Decentralized People is the union of independent developers formed to collaborate on the projects that bring positive changes to the global society
                            </h1>
                        </div>
                    </div>
                    <div className="curve" />
                </div>

                {/* this is the second section with the project button and description */}
                <div className="home-secBlack">
                    <div className="homeFlex">
                        <div className="description-pos-left">
                            <h1 style={{ color: "white" }}>
                                Let us introduce the projects we've been working on
                            </h1>
                        </div>
                        <div className="bg-Blob">
                            <Link to='/projects' style={{ color: 'transparent' }}>
                                <Blob size="55.5vh" className="blob">
                                    Projects
                                </Blob>
                            </Link>
                        </div>
                    </div>
                    <div className="curve2" />
                </div>

                {/* this is the third section with the developer button and description */}
                <div className="home-sec">
                    <div className="homeFlex">
                        <div className="bg-Blob">
                            <Link to='/developers' style={{ color: 'transparent' }}>
                                <Blob size="55.5vh" className="blobReverse">
                                    Developers
                                </Blob>
                            </Link>
                        </div>

                        <div className="description-pos">
                            <h1> And of course each project has people behind </h1>
                        </div>
                    </div>
                    <div className="curve3" />
                </div>

                {/* this will be the form to join */}
                <div className="home-secBlack" id="join">
                    <Section
                        title="Please fill in the form we would love to know more about you!"
                        subtitle={ContactForm}
                        dark={true}
                        id="joinUs"
                    />
                </div>
                <div className="curve4" />
            </div>


        </>
    );

}

